<template>
  <div class="con">
    <div class="login">
      <h3>登录</h3>
      <el-input v-model="user.username" placeholder="请输入用户名"></el-input>
      <el-input
        v-model="user.password"
        type="password"
        placeholder="请输入密码"
      ></el-input>
      <el-button native-type="submit " @click="login" type="primary"
        >登录</el-button
      >
    </div>
  </div>
</template>
 
<script>
import { warningAlert,successAlert } from '../../util/alert';
import { mangerLogina } from "../../util/request";
import {mapActions} from "vuex"
export default {
  data() {
    return {
      user: {
        username: "",
        password: "",
      },
    };
  },
  methods: {
    ...mapActions({
      userRequest:"userActions"
    }),
    login() {
      //将信息存储在在localstorage
      // localStorage.removeItem("user")
      // console.log(JSON.parse(localStorage.getItem("user")));
      mangerLogina(this.user).then((res) => {
         this.userRequest(res.data.list)
        if (res.data.code == 200) {
          successAlert(res.data.msg)
          // localStorage.setItem("user",JSON.stringify(res.data.list))

          this.userRequest(res.data.list)
          this.$router.push("/")
          return
        }else{
          warningAlert(res.data.msg)
        }
      });
    },
  },
};
</script>

<style scoped>
.con {
  width: 100vw;
  height: 100vh;
  position: flex;
  left: 0;
  top: 0;
  background: linear-gradient(to right, #543545, #343c5c);
}
.login {
  width: 300px;
  height: 200px;
  padding: 20px 50px 50px 50px;
  position: absolute;
  top: 140px;
  left: 50%;
  transform: translate(-50%, 0);
  background: #fff;
  text-align: center;
}
h3 {
  margin-bottom: 20px;
}
.el-input {
  margin-bottom: 20px;
}
.el-button {
  width: 100%;
}
</style>


